class Maquetacion extends React.Component {
  constructor(props){
    super(props);
    
  }
  componentDidMount() {
    document.body.style.backgroundImage = "url(" + fondoImgDos + ")";
  }
  componentWillUnmount() {
    document.body.style.backgroundImage = "url(" + fondoImgDos + ")";
  }

  render() {
    return (
      <div class="W-100">
        <Helmet>
          <title>MAQUETACIO</title>
          <meta name="description" content="Aplicacion para crear dibujos" />
        </Helmet>
        <h2 class="p-cv-center">Maquetacin pdf</h2>
        <div class="container mt-5 mb-1">
          <div class="row">
              {datas.map((item, i) => 
                  <div class="col-lg-4 col-md-4 mt-4">
                    <FlipX
                      title={item.name}
                      liga={item.liga}
                      second={item.titleUno}
                      third={item.titleDos}
                      img={item.imagen}
                      ids={i}
                      parrafos={item}
                    />
                  </div>
              )}
          </div>
        </div>
        <h2 class="p-cv-center mt-5 mb-1">Maquetacin web</h2>
        <div class="container">
          <div class="row">
            {datas_web.map((item, i) => <div class="col-lg-4 col-md-4 pt-5">
              <FlipY
                  title={item.name}
                  liga={item.liga}
                  second={item.titleUno}
                  third={item.titleDos}
                  img={item.imagen}
                  ids={i}
                  parrafos={item}
                /></div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export { Maquetacion };
